import * as util from '@/utils';

export default {
	data() {
		return {
			describe: "", //描述
			buttonText: "" //按钮名称
		};
	},
	onLoad(options) {
		this.options = JSON.parse(options.data)
		this.isInit = true
		this.buttonText = this.options.buttonText
		this.describe = this.options.describe
	},

	onShow() {
		if (this.isInit) {
			this.isInit = false
			return
		}

		this.$api.showBusy()

		//重新检测
		util.detectAccredit(this.options.scope).then(() => {
			this.$api.hideBusy()
			this.$api.gotoPage("back")
		}).catch(() => {
			// 没有授权
			this.$api.hideBusy()
			this.$api.showModal({
				title: '温馨提示',
				showCancel: false,
				content: this.options.content
			})
		})
	},
	methods: {
		/**
		 * 必须手动启动
		 * 打开设置
		 */
		onOpenSetting() {
			uni.openSetting()
		}
	}
}
